.backdrop {
  z-index: 999;
  background-color: #0003;
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  inset: 0;
}

.backdrop.show {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.backdrop.show .modal, .backdrop:not(.is__hidden-contact) .modal {
  opacity: 1;
  transform: translate(-50%, -50%)scale(1);
}

.backdrop.is-hidden-contact {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal {
  opacity: 0;
  background-color: #fcfcfb;
  border-radius: 16px;
  padding: 50px 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(.5);
  box-shadow: 0 2px 1px #0003, 0 1px 1px #00000024, 0 1px 3px #0000001f;
}

